.interaction-menu{
  background: #e4e4e4;
  margin: 40px;
  min-width: 500px;
  border-radius: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
}



.save-btn{
  margin: 5px;

  cursor: pointer;
  height: 35px;
  border-radius: 25px;
  padding: 0;
  outline: none;
  border: none;
}

.download-btn button:hover {
  text-decoration: underline;
  color: rgb(219, 219, 219);
  background:#851e3e;
  box-shadow: inset 0 0 0 4px #FFF;
} 

.position-btns{
  display: flex;
  /* padding: 5px; */
}



.position-btn-wrapper{
  margin: 5px;
  background-color: #939597;
  display: flex;
  width: 35px;
  height: 35px;
  border-radius: 10px;
  position: relative;
  justify-content: center;
  align-items: center;
  
}

.position-btn{
  cursor: pointer;
  background-color: #DBDBDB;
  width: 30px;
  height: 30px;
  border-radius: 10px;
  outline: none;
  border: 1px solid #1E1E1E;
  display: flex;
  position: relative;

}

.position-inner{
  background-color: #F5DF4D;
  width: 18px;
  height: 18px;
  border-radius: 5px;
  transition: all 0.3s;
  border: 1px solid #1E1E1E;
  position: absolute;
}

#position-center-inner{
  background-color: #851e3e;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}
#position-left-inner{
  top: 50%;
  left: 0%;
  transform: translate(0,-50%);
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  border-left: none;
}
#position-right-inner{
  top: 50%;
  left: 100%; transform:translate(-100%,-50%);
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
  border-right: none;
}
#position-top-inner{
  border-top: none;
  left: 50%;
  top: 0; transform:translate(-50%, 0);
  border-top-right-radius: 3px;
  border-top-left-radius: 3px;

}
#position-bottom-inner{
  border-bottom: none;
  left: 50%;
  top: 100%; transform:translate(-50%, -100%);
  border-bottom-right-radius: 3px;
  border-bottom-left-radius: 3px;
  transition: all 0.3s;
}



.save-btns-container{
  display: flex;
}



@media only screen 
  and (max-device-width: 667px){
    .interaction-menu{
      min-width: 200px;
      max-width: 400px;
      flex-wrap: wrap;
    }
  }