@import url(https://fonts.googleapis.com/css2?family=Hammersmith+One&family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*::before, *::after {
  box-sizing: border-box;
}

body{
  background-color: #393E46;
}


canvas{
  display: none;
}

canvas img{
  display: none;
}
.patterns-scroll::-webkit-scrollbar{
  background-color: #939597 !important;
}
.scroll::-webkit-scrollbar{
  width: 12px;
  background-color: #393E46;
  border-radius: 25px;
}

.scroll::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	/* -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3); */
	background-color: #e4e4e4;
}


.project-title{
  color: #F5DF4D;
  text-align: center;
  margin-top: 20px;
  letter-spacing: 5px;
  font-size: 1.5rem;
  font-family: 'Hammersmith One', sans-serif;
}


@media only screen 
  and (min-device-width: 375px) 
  and (max-device-width: 667px){
    .project-title{
  font-size: 1rem;

    }
  }
.pattern-btn{
  min-width: 200px;
  min-height: 120px;
  outline: none;
  border: none;
  border-radius: 15px;
  cursor: "pointer";
  margin: 0 15px 15px;
  background-position: center center;
  background-repeat: repeat;
  cursor: pointer;
  /* transition: all 0.4s; */
}
.background{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 30px;
}
.patterns-container{
  width: 90%;
  max-width: 1400px;
}
.patterns-btns{
  margin-top: 15px;
  display: flex;
  min-width: 190px;
  overflow-x: auto;
  display: flex;
  overflow-x: scroll;
  flex-wrap: nowrap;
  -webkit-overflow-scrolling: touch;

  overflow-y: hidden;
    white-space: nowrap;
    flex-wrap: nowrap;
}

.active{
  margin: auto;
  transition: morphing 0.1s;
  
	/* background-color: #21D4FD; */
	/* background-image: linear-gradient(19deg, #21D4FD 0%, #B721FF 100%); */
  border-radius: 30% 70% 70% 50% / 30% 30% 70% 70%;
	/* box-shadow: 15px 15px 50px rgba(0,0,0,0.2); */
	animation: morphing 10s infinite;
	overflow: hidden;}
	

  @keyframes morphing {
		0% {
      /* border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%; */
      border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
      /* box-shadow: 15px 15px 50px rgba(0,0,0,0.2); */
		}
		25% { 
      border-radius: 58% 42% 75% 25% / 76% 46% 54% 24%;
      
		}
		50% {
			border-radius: 50% 50% 33% 67% / 55% 27% 73% 45%;
      /* box-shadow: -10px -15px 10px rgba(0,0,0,0.1); */
      
		}
		75% {
      border-radius: 33% 67% 58% 42% / 63% 68% 32% 37%;
    }
    100%{
      border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
    }
	}
#img-section{
  box-shadow:
  0 20px 25px -5px rgba(0,0,0, .1),
  0 10px 15px -5px rgba(0,0,0, .1),
  10px 0 15px -5px rgba(0,0,0, .1),
  -10px 0 15px -5px rgba(0,0,0, .1);
  border-radius: 25px;
}
.bg{
position: relative;
transition: all 0.3s;
border-radius: 25px;
height: 100%;
}

#SVG{
  border-radius: 25px;
}
.image{
  max-width: 900px;
  max-height: 900px;
  cursor: pointer;
  transition: all .4s;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);

  border-radius: 25px;
  /* box-shadow:
  0 20px 25px -5px rgba(0,0,0,.1), 0 10px 10px -5px rgba(0,0,0,.4); */
  overflow: hidden;
}





.start-msg{
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  background-color: #851e3e;
  width: 390px; height: 200px;
  color: rgb(219, 219, 219);
  border-radius: 25px;
  padding: 5px 23px;
  display: flex;
  flex-direction: column;
  font-family: 'Roboto', sans-serif;
  cursor: pointer;
  align-content: center;
  text-align: center;
}

.greeting h1{
  font-size: 2rem;
  margin-bottom: 5px;
}

.start-about{
  
  font-size: 1.3rem;
  margin-top: 15px;
}

.get-started{
  margin-top: 30px;
}


@media only screen 
  and (max-device-width: 667px){
    .bg{
      width: 330px ;
      height: 450px;
    }
    .image{
      max-width: 250px;
      max-height: 600px;
    }
    .start-msg{
      width: 250px; height: 250px;

    }
  }
.background-color-btn{
  outline: none;
  border: none;
  margin: 5px;
  min-width: 80px;
  min-height: 80px;
  cursor: pointer;
  border-radius: 25px;
}

.foreground-color-btn{
  outline: none;
  border: none;
  margin: 5px;
  min-width: 80px;
  min-height: 80px;
  border-radius: 25px;
  cursor: pointer;
}

.colors{
  background: #939597;
  color: #eeeeee;
  padding: 40px;
  margin: 30px 20px 100px;
  width: 90%;
  max-width: 1400px;
  overflow: hidden;
  border-radius: 25px;
  box-shadow:
  0 20px 25px -5px rgba(0,0,0, .2),
  0 10px 15px -5px rgba(0,0,0, .1),
  10px 0 15px -5px rgba(0,0,0, .2),
  -10px 0 15px -5px rgba(0,0,0, .2);

}

.background-colors-container{
  display: flex;
  overflow-x: scroll;
  flex-wrap: nowrap;
margin: 10px 0px 30px;
}


.foreground-colors-container{
  display: flex;
  overflow-x: auto;
  flex-wrap: nowrap;
  /* -webkit-overflow-scrolling: touch; */

}

@media only screen 
  and (max-device-width: 667px){
    .colors{
      min-width: 300px;
      max-width: 500px;
      margin: 30px 0px;
      font-size: 0.8rem;
    }
    .foreground-colors-container{
      width: 100%;
    }
  }
.interaction-menu{
  background: #e4e4e4;
  margin: 40px;
  min-width: 500px;
  border-radius: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
}



.save-btn{
  margin: 5px;

  cursor: pointer;
  height: 35px;
  border-radius: 25px;
  padding: 0;
  outline: none;
  border: none;
}

.download-btn button:hover {
  text-decoration: underline;
  color: rgb(219, 219, 219);
  background:#851e3e;
  box-shadow: inset 0 0 0 4px #FFF;
} 

.position-btns{
  display: flex;
  /* padding: 5px; */
}



.position-btn-wrapper{
  margin: 5px;
  background-color: #939597;
  display: flex;
  width: 35px;
  height: 35px;
  border-radius: 10px;
  position: relative;
  justify-content: center;
  align-items: center;
  
}

.position-btn{
  cursor: pointer;
  background-color: #DBDBDB;
  width: 30px;
  height: 30px;
  border-radius: 10px;
  outline: none;
  border: 1px solid #1E1E1E;
  display: flex;
  position: relative;

}

.position-inner{
  background-color: #F5DF4D;
  width: 18px;
  height: 18px;
  border-radius: 5px;
  transition: all 0.3s;
  border: 1px solid #1E1E1E;
  position: absolute;
}

#position-center-inner{
  background-color: #851e3e;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}
#position-left-inner{
  top: 50%;
  left: 0%;
  transform: translate(0,-50%);
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  border-left: none;
}
#position-right-inner{
  top: 50%;
  left: 100%; transform:translate(-100%,-50%);
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
  border-right: none;
}
#position-top-inner{
  border-top: none;
  left: 50%;
  top: 0; transform:translate(-50%, 0);
  border-top-right-radius: 3px;
  border-top-left-radius: 3px;

}
#position-bottom-inner{
  border-bottom: none;
  left: 50%;
  top: 100%; transform:translate(-50%, -100%);
  border-bottom-right-radius: 3px;
  border-bottom-left-radius: 3px;
  transition: all 0.3s;
}



.save-btns-container{
  display: flex;
}



@media only screen 
  and (max-device-width: 667px){
    .interaction-menu{
      min-width: 200px;
      max-width: 400px;
      flex-wrap: wrap;
    }
  }
