.background-color-btn{
  outline: none;
  border: none;
  margin: 5px;
  min-width: 80px;
  min-height: 80px;
  cursor: pointer;
  border-radius: 25px;
}

.foreground-color-btn{
  outline: none;
  border: none;
  margin: 5px;
  min-width: 80px;
  min-height: 80px;
  border-radius: 25px;
  cursor: pointer;
}

.colors{
  background: #939597;
  color: #eeeeee;
  padding: 40px;
  margin: 30px 20px 100px;
  width: 90%;
  max-width: 1400px;
  overflow: hidden;
  border-radius: 25px;
  box-shadow:
  0 20px 25px -5px rgba(0,0,0, .2),
  0 10px 15px -5px rgba(0,0,0, .1),
  10px 0 15px -5px rgba(0,0,0, .2),
  -10px 0 15px -5px rgba(0,0,0, .2);

}

.background-colors-container{
  display: flex;
  overflow-x: scroll;
  flex-wrap: nowrap;
margin: 10px 0px 30px;
}


.foreground-colors-container{
  display: flex;
  overflow-x: auto;
  flex-wrap: nowrap;
  /* -webkit-overflow-scrolling: touch; */

}

@media only screen 
  and (max-device-width: 667px){
    .colors{
      min-width: 300px;
      max-width: 500px;
      margin: 30px 0px;
      font-size: 0.8rem;
    }
    .foreground-colors-container{
      width: 100%;
    }
  }